// Vars

:root {
  // base
  --sl-font-sans: 'Comfortaa';
  --base-txt-color: #404040;
  --heading-color: #333;
  --base-font-family: 'Comfortaa', sans-serif;
  --body-bg: #fff;
  

  // colors  
  $brand-color: #9DB512;
  --brand-color: #{$brand-color};
  --link-color: --brand-color;

  --sl-color-primary: #00A0EE;

  // Reward Explorers colors
  --re-blue: var(--sl-color-primary);
  --re-bluegreen: #36A2AF;
  --re-light-green: #9DB512;
  --re-dark-green: #517700;
  --re-orange: #FF7600;

  --re-light-green-bg: #9db512d3;

  
  // app header
  --app-header-height: 70px;
  --app-header-txt-color: #fff;
  --app-header-title-font-size: 1.6em;

  // shoelace tokens
  // full list here:
  // https://github.com/shoelace-style/shoelace/blob/current/src/styles/base.scss
  --sl-color-primary-50: rgb(243 251 254);
  --sl-color-primary-100: rgb(215 240 252);
  --sl-color-primary-200: rgb(184 229 250);
  --sl-color-primary-300: rgb(149 215 248);
  --sl-color-primary-400: rgb(99 197 245);
  --sl-color-primary-500: rgb(25 169 240);
  --sl-color-primary-600: rgb(0 139 207);
  --sl-color-primary-700: rgb(0 114 170);
  --sl-color-primary-800: rgb(0 94 141);
  --sl-color-primary-900: rgb(0 67 100);
  --sl-color-primary-950: rgb(0 42 62);


  --sl-color-success: var(--re-dark-green);
  --sl-color-warning: var(--re-orange);

  --sl-color-success-50: rgb(249 250 240);
--sl-color-success-100: rgb(234 239 205);
--sl-color-success-200: rgb(219 228 167);
--sl-color-success-300: rgb(201 214 124);
--sl-color-success-400: rgb(176 196 65);
--sl-color-success-500: rgb(147 170 17);
--sl-color-success-600: rgb(122 140 14);
--sl-color-success-700: rgb(100 115 11);
--sl-color-success-800: rgb(82 95 9);
--sl-color-success-900: rgb(59 67 7);
--sl-color-success-950: rgb(36 41 4);

--sl-color-warning-50: rgb(255 248 242);
--sl-color-warning-100: rgb(255 232 213);
--sl-color-warning-200: rgb(255 215 180);
--sl-color-warning-300: rgb(255 194 142);
--sl-color-warning-400: rgb(255 165 88);
--sl-color-warning-500: rgb(255 121 6);
--sl-color-warning-600: rgb(213 99 0);
--sl-color-warning-700: rgb(175 81 0);
--sl-color-warning-800: rgb(145 67 0);
--sl-color-warning-900: rgb(103 48 0);
--sl-color-warning-950: rgb(64 30 0);


}